/// Default styles for mendix Layout Grid.
/// Adapted from Atlas v2.4.1 Layout Grid, itself based on Bootstrap v4 grid.
/// Major changes from default: our layout grid has no padding on the main container. This allows
/// nested layout grids without need to reset padding or contextual overrides which blow out specificity
/// Instead, use a separate .pds-container to wrap content and provide default padding.
/// @example
///  // for apps which use the compiled pds.css
///  // in ./<your-app>/no-control/_layout-grid.scss
///  @use "../mixins/layout-grid";
///  @include layout-grid.styles;
///
/// @example
///  // for apps which still @use "pds" in <your-app>.scss, turn it on in settings.
///  // in <your-app>.scss
///  @use "../pds/settings" with ($use-custom-layout-grid: true)
///
/// @tag tag-layout tag-grid

@use "../constants/layout";
@use "../mixins";

$_gutter-sm: layout.$gutter-size-small;
$_gutter: layout.$gutter-size-regular;

@mixin _reset {
  // Atlas < 2.3.1 see core/_reset.scss
  // Atlas > 2.4.1 see core/base/_spacing.scss
  .mx-scrollcontainer .mx-scrollcontainer-wrapper .mx-layoutgrid-fixed {
    max-width: layout.$visual-max-width;
  }

  .mx-scrollcontainer .mx-placeholder {
    .mx-layoutgrid,
    .mx-layoutgrid-fluid {
      &,
      .mx-layoutgrid,
      .mx-layoutgrid-fluid {
        padding: 0;
      }
    }
  }
}

