      margin-left: auto;
      padding-right: 0;
      padding-left: 0;

      @include mixins.for-tablet-portrait-up {
        --gutter-size: #{$_gutter};
      }
    }

    // Fixed setting is not recommended, wrap it in a .pds-container instead.
    .mx-layoutgrid-fixed {
      max-width: layout.$visual-max-width;
    }

    // Row
